.hero-inner__background-image {
  min-width: 1200px;
  min-height: 518px;
  background-color: #dceaf3;
  background-image: url("../../../img/hero-background.jpg");
  background-repeat: no-repeat;
}

.hero-inner__background-image::after {
  display: flex;
  position: relative;
  content: "";
  min-width: 100%;
  min-height: 57px;
  background-color: transparent;
  background-image: url("../../../img/divider.svg");
  background-repeat: no-repeat;
}

.hero-inner__title {
  font-size: 30px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
}
